body {
  margin: 0;
}

img {
  display: block;
}

p {
  margin: 0;
  padding: 0;
}

.search-form {
  width: 100vw;
  background-color: #00f;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 15px;
  display: flex;
  position: fixed;
  box-shadow: 0 2px 6px rgba(0, 0, 0, .3);
}

.search-form input {
  box-shadow: none;
  border: 2px solid #ccc;
  border-radius: 4px;
  outline: none;
  margin-right: 4px;
  padding: 10px;
  font-size: 16px;
}

.search-form button {
  color: #fff;
  cursor: pointer;
  background-color: #3470f3;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .25s;
}

.search-form button:hover {
  background-color: #0b2d76;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin: 0 15px;
  padding-top: 100px;
  display: flex;
}

.photo-card {
  width: 340px;
  background-color: #fff;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 4px 10px rgba(0, 0, 0, .1);
}

.photo-card img {
  width: 100%;
  height: 220px;
  object-fit: cover;
}

.info {
  justify-content: center;
  gap: 15px;
  padding: 15px;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.load-more-thumb {
  width: 100%;
  flex-direction: column;
  display: flex;
}

.load-more, .colection-end {
  color: #fff;
  cursor: pointer;
  background-color: #3470f3;
  border: none;
  border-radius: 4px;
  margin: 10px auto;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .25s;
}

.colection-end {
  cursor: auto;
  opacity: .75;
  background-color: green;
  display: block;
}

.load-more:hover {
  background-color: #0b2d76;
}

.is-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

